:root {
  --black: #212529;
  --white: #f8f9fa;
  --lightGrey: #ced4da;
  --mediumGrey: #6c757d;
  --darkGrey: #343a40;
  box-sizing: border-box;
  background-color: var(--mediumGrey);
}

/*#region Grids*/

.Album {
  padding-top: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.Title {
  grid-area: Title;
}

.Photo {
  grid-area: Photo;
  width: 100%;
}

.Download {
  grid-area: Download;
}

.Buttons {
  grid-area: Buttons;
  display: grid;

  grid-template-areas: "PrevsButton NextButton";
}

.PrevButton {
  grid-area: PrevsButton;
}

.NextButton {
  grid-area: NextButton;
}
/*#endregion*/

/*#region Loader*/
.loader {
  margin: 10px;
  border: 1rem solid #0c0032;
  border-top: 1rem solid #3500d3;
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  animation: spin 2s both infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/*#endregion*/

/*#region Gallery*/
.gallery {
  border: 1px solid #ccc;
}

.gallery:hover {
  border: 1px solid #777;
}

.gallery img {
  width: 100%;
  height: auto;
}

.desc {
  padding: 15px;
  text-align: center;
}

.responsive {
  padding: 0 6px;
  float: left;
  width: 24.99999%;
}

@media only screen and (max-width: 700px) {
  .responsive {
    width: 49.99999%;
    margin: 6px 0;
  }
}

@media only screen and (max-width: 500px) {
  .responsive {
    width: 100%;
  }
}

.clearfix:after {
  content: "";
  display: table;
  clear: both;
}
/*#endregion*/

/*#region Layouts*/
img {
  width: 90%;
  border-radius: 20%;
}

.flex-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 90%;
}

.vertical-center {
  margin: 0;
  position: absolute;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
/*#endregion*/

/*#region Links*/

.Album a {
  background-color: var(--mediumGrey);
  color: var(--white);
  text-decoration: none;
  border: 2px solid var(--black);
  padding: 10px 20px;
  text-align: center;
}
.Album a:hover {
  color: var(--lightGrey);
  text-decoration: underline;
}
/*#endregion*/

/*#region Media Queries*/
@media only screen and (max-width: 600px) {
  .Album {
    display: inline;
  }
}
/*#endregion*/

/*#region NavBar*/
nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-width: 10vh;
  background: var(--darkGrey);
  color: white;
}

.navLinks {
  width: 40%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}

.NavLink {
  width: 40%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}
/*#endregion*/
